<template>
  <div class="film-box" style="height:100%">
    <div class="content" style="padding-top: 1.35rem;" :style="{ minHeight: curHeight+'px'}">
      <div class="nav">
        <div class="navUser clear-fix">
          <img @click="toFront()" class="fl img" src="../../assets/image/market_details/back.png" alt="">
          <div class="fl">
            <van-dropdown-menu >
            <van-dropdown-item @change="ccc" v-model="value" :options="option" />
          </van-dropdown-menu>
          </div>
        </div>
      </div>
      <!-- 数字 金额-->
      <div class="nav-num">
        <div class="nav-num-left red-font" style="font-size: 0.42rem;">{{current_price_usd}} <span style="font-size: 0.24rem;color: #767884;">￥ {{FunHtml(current_price_usd)}}</span></div>
        <div class="nav-num-right red-font" style="font-size: 0.24rem;">{{scale}}%</div>
      </div>
      <!-- 成交量区域-->
      <div class="nav-volume">
        <div class="cjl">
          <div class="inner-box">
            <p style="color:rgba(255,255,255,0.7);">{{textTOfixed(onlycoinList[10])}}</p>
            <p>24H成交量</p>
          </div>
        </div>
        <div class="zg">
          <div class="inner-box">
            <p style="color:rgba(255,255,255,0.7);">{{textTOfixed(onlycoinList[4])}}</p>
            <p>最高</p>
          </div>
        </div>
        <div class="zd">
          <div class="inner-box">
            <p style="color:rgba(255,255,255,0.7);">{{textTOfixed(onlycoinList[3])}}</p>
            <p>最低</p>
          </div>
        </div>
      </div>
      <div class="middle-box">
        <div id="chart_example"></div>
      </div>
      <!--bottom 委托订单 -->
      <div class="bottom-tab">
        <div class="bottom-tab-left">
          <span>委托订单</span>
          <span>最新成交</span>
        </div>
        <!-- <div class="bottom-tab-right">
          <div class="hui">
            <span>汇率</span>
            <img src="../../assets/image/market_details/rmb.png" alt="">
          </div>
          <div class="yujing">
            <span>预警</span>
            <img src="../../assets/image/market_details/bell.png" alt="">
          </div>
        </div> -->
      </div>
      <!--下部展示区域-->
      <div class="bottom-box">
        <div class="thead">
        <div class="thead-left">
          <span class="green-btn"></span>买盘
          <span class="red-btn"></span>卖盘
        </div>
        <!-- <div class="thead-right">
          777 <img src="../../assets/image/market_details/select.png" alt="">
        </div> -->
        </div>
        <!--内容循环列表区域-->
        <!-- <div v-for="(item,index) in 20" :key="index"> -->
        <div class="list-item clear-fix">
          <!-- 左边 -->
          <div style="width: 48%;" class="fl">
            <div class="clear-fix" v-for="(item,index) in depthList.bids" :key="index" style="margin-bottom: 0.05rem;">
              <div class="box fl" style="width: 50%;">{{FunText(item[0])}}</div>
              <div class="box green-font green-bg fl" style="width: 50%;">{{FunText(item[1])}}</div>
            </div>
          </div>
          <!-- 右边 -->
          <div style="width: 48%;" class="fr">
            <div class="clear-fix" v-for="(item,index) in depthList.asks" :key="index" style="margin-bottom: 0.05rem;">
              <div class="box fl red-font red-bg" style="width: 50%;">{{FunText(item[0])}}</div>
              <div class="box fl" style="width: 50%;">{{FunText(item[1])}}</div>
            </div>
            <!-- <div class="list-item-right" v-for="(item,index) in 2" :key="index">
              <span class="red-font red-bg">0.88899</span>
              <span>999999</span>
            </div> -->
          </div>
        </div>
      </div>
      <!--底部按钮-->
      <div class="bnt-box">
        <div class="bnt-left" @click="userShow(1)">买入</div>
        <div class="bnt-right" @click="userShow(2)">卖出</div>
      </div>
      <!-- 弹窗部分 -->
      <div class="posiBox" v-if="isShow" @click="showBox()"></div>
      <div class="ventanal" v-if="isShow">
        <div class="title">立即参与</div>
        <div class="isTips" style="margin-bottom: 0.1rem;">
          <span class="num" style="font-size: 0.3rem;">
            <span style="width: 1.2rem;display: inline-block;">币&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;种: </span>
            <span class="num1" style="color:#3DD078">{{bibisellData.symbol}}</span>
          </span>
        </div>
        <div class="iptBox">
          <span class="text">数量</span>
          <input class="ipt" type="number"  @input="changeInput" v-model="iptNum" placeholder="请输入数量">
        </div>
        <div class="iptBox">
          <span class="text">交易密码</span>
          <input class="ipt" type="password" v-model="paypass" placeholder="请输入交易密码">
        </div>
        <div class="isTips" style="margin-bottom: 0.1rem;margin-top: 0.3rem;">
          <span class="num" style="font-size: 0.3rem;">
            <span style="width: 1.2rem;display: inline-block;">总额度：</span>
            <span class="num1" style="color:#3DD078">{{totalAmount}}</span>
          </span>
        </div>
        <div></div>
        <div class="subBox" @click="userSub(type)">提交</div>
      </div>
    <!-- 弹窗结束 -->
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
import { depth, coin, bibisell, bibibuy, onlycoin, marketList } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { DropdownMenu, DropdownItem, Toast } from 'vant'
Vue.use(DropdownMenu).use(DropdownItem).use(Toast)
export default {
  components: {},
  data () {
    return {
      value: 0,
      option: [],
      depthList: [],
      curHeight: '',
      lineList: [], // k线返回数据
      coinData: {
        market: 'coin',
        page: 1,
        showpage: 10
      },
      isShow: false, // 弹窗是否显示
      current_price_usd: '', // 当前价格
      iptNum: '', // 弹窗输入的数量
      paypass: '', // 弹窗交易密码
      totalAmount: 0, // 总额度,
      resdata24: {}, // 24Kline
      scale: '',
      // 盘口对象
      depthData: {
        market: 'depth',
        symbol: 'btc'
      },
      // 币币购买
      bibisellData: {
        u_id: '', // 用户ID
        symbol: '', // 币种
        price: '', // 消耗总额度
        num: '', // 获得数量
        paypass: '' // 交易密码
      },
      // 币币卖出
      bibibuyData: {
        u_id: '10',
        symbol: 'btc', // 币种
        price: '', // 获得数量
        num: 1, // 消耗数量
        paypass: '12345678' // 交易密码
      },
      // 最高最低
      onlycoinData: {
        market: 'onlycoin',
        symbol: 'btc'
      },
      onlycoinList: [],
      type: '' // 判断是买入还是卖出 1 买入 2 买出
    }
  },
  created () {
    console.log('打印传过来的值', this.$route.query.adUrl)
    this.bibisellData.u_id = sessionStorage.getItem('u_id') // 市场批量买入id
    this.depthFun() // 盘口
    this.coinFun() // 币种
    // this.depthFun()
    this.onlycoinFun() // z最高最低
    this.data24() // 24kline
  },
  methods: {
    textTOfixed (type) {
      var types = Number(type)
      var text = types.toFixed(2)
      return text
    },
    // 最高最低量
    onlycoinFun () {
      var that = this
      // that.onlycoinData.symbol = that.bibisellData.symbol
      var data = objKeySort(this.onlycoinData)
      var token = sgin(data)
      this.onlycoinData.token = token
      onlycoin(that.onlycoinData).then(res => {
        if (res.code === 0) {
          console.log(res, '最高最低')
          that.onlycoinList = res.data
          console.log(that.onlycoinList[0], res.data)
          // that.$toast({ message: '', duration: 1500 })
          delete that.onlycoinData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.onlycoinData.token
      })
    },
    userShow (type) {
      this.isShow = true
      this.type = type
    },
    // 买入
    bibisellFun () {
      var that = this
      // var totalAmount = ''
      that.totalAmount = (Number(that.current_price_usd) * Number(that.iptNum)).toFixed(4)
      that.bibisellData.price = that.totalAmount
      that.bibisellData.num = that.iptNum
      that.bibisellData.paypass = that.paypass
      var data = objKeySort(this.bibisellData)
      var token = sgin(data)
      this.bibisellData.token = token
      bibisell(that.bibisellData).then(res => {
        if (res.code === 0) {
          // this.depthList = res.data
          that.$toast({ message: '买入成功', duration: 1500 })
          that.iptNum = ''
          that.totalAmount = 0
          that.paypass = ''
          that.isShow = false
          // that.usdt_available_balance -= that.totalAmount
          delete that.bibisellData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.isShow = false
        that.iptNum = ''
        that.totalAmount = 0
        that.paypass = ''
        delete that.bibisellData.token
      })
    },
    // 卖出
    bibibuyFun () {
      var that = this
      that.totalAmount = (Number(that.current_price_usd) * Number(that.iptNum)).toFixed(4)
      that.bibibuyData.price = that.totalAmount
      that.bibibuyData.num = that.iptNum
      that.bibibuyData.paypass = that.paypass
      // ==========
      var data = objKeySort(this.bibibuyData)
      var token = sgin(data)
      that.bibibuyData.token = token
      console.log(that.bibibuyData, 'bibibuyData')
      bibibuy(that.bibibuyData).then(res => {
        if (res.code === 0) {
          that.$toast({ message: '卖出成功', duration: 1500 })
          that.iptNum = ''
          that.totalAmount = 0
          that.paypass = ''
          that.isShow = false
          // that.usdt_available_balance = Number(that.usdt_available_balance) + Number(that.totalAmount)
          delete that.bibibuyData.token
        }
      }).catch((err) => {
        console.log(err, '11')
        that.$toast({ message: err, duration: 1500 })
        that.iptNum = ''
        that.totalAmount = 0
        that.paypass = ''
        that.isShow = false
        delete that.bibibuyData.token
      })
    },
    userSub () {
      var that = this
      if (that.iptNum === '') {
        that.$toast({ message: '数量不能为空', duration: 1500 })
        return
      }
      if (that.paypass === '') {
        that.$toast({ message: '交易密码不能为空', duration: 1500 })
        return
      }
      if (this.type === 1) {
        this.bibisellFun()
      } else if (this.type === 2) {
        this.bibibuyFun()
      }
    },
    showBox () {
      this.isShow = false
      this.iptNum = ''
      this.totalAmount = 0
      this.paypass = ''
    },
    changeInput () {
      var that = this
      this.$nextTick(() => {
        console.log(that.iptNum, that.current_price_usd)
        this.totalAmount = (Number(that.current_price_usd) * Number(that.iptNum)).toFixed(4)
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    // 盘口
    depthFun () {
      var that = this
      var data = objKeySort(this.depthData)
      var token = sgin(data)
      // this.option = []
      this.depthData.token = token
      depth(this.depthData).then(res => {
        if (res.code === 0) {
          this.depthList = res.data
          delete that.depthData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.depthData.token
      })
    },
    FunText (type) {
      var text = Number(type)
      var conTxt = text.toFixed(4)
      return conTxt
    },
    FunHtml (type) {
      var text = Number(type)
      var conTxt = (text * 7).toFixed(4)
      return conTxt
    },
    // 币种列表
    coinFun () {
      var that = this
      var data = objKeySort(this.coinData)
      var token = sgin(data)
      this.coinData.token = token
      coin(this.coinData).then(res => {
        console.log('币种列表', res)
        that.list = res.data
        for (const [i, v] of res.data.entries()) {
          var arr = {
            text: '',
            value: 0,
            current_price_usd: '',
            scale: ''
          }
          if (v.name === 'USDT/USDT') {
          } else {
            arr.text = v.name
            arr.value = i
            arr.current_price_usd = v.current_price_usd
            arr.scale = v.scale
            that.option.push(arr)
          }
        }
        this.scale = this.option[0].scale
        var symbol = this.option[0].text.split('/')
        that.bibisellData.symbol = symbol[0].toLowerCase()
        that.bibibuyData.symbol = symbol[0].toLowerCase()
        that.onlycoinData.symbol = symbol[0].toLowerCase()
        this.current_price_usd = this.option[0].current_price_usd
        delete that.coinData.token
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.coinData.token
      })
    },
    toFront () {
      history.go(-1)
    },
    ccc (value) {
      console.log('type', value)
      var that = this
      for (const [i, v] of that.option.entries()) {
        if (v.value === value) {
          console.log('等于了', i, v, v)
          that.scale = v.scale
          that.current_price_usd = v.current_price_usd
          var symbol = v.text.split('/')
          that.bibisellData.symbol = symbol[0].toLowerCase()
          // .....
          that.bibibuyData.symbol = symbol[0].toLowerCase()
          // .....
          that.onlycoinData.symbol = symbol[0].toLowerCase()
          that.depthFun()
          that.onlycoinFun()
        }
      }
    },
    // 24小时K线
    data24 () {
      var that = this
      that.resdata24.market = 'data'
      that.resdata24.symbol = 'btc'
      that.resdata24.period = '1440'
      that.resdata24.size = 100
      var data = objKeySort(this.resdata24)
      var token = sgin(data)
      that.resdata24.token = token
      marketList(that.resdata24).then(res => {
        if (res.code === 0) {
          console.log(res.data, '24data')
          that.lineList = res.data
          that.lineData()
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.onlycoinData.token
      })
    },
    // k线方法
    generateOHLC (count) {
      var data = [
        // ['2011-01-01↵00:00:00', 6118.82, 6118.82, 6108.63, 6110.56, '7149871', -1],
        // ['2011-01-01↵00:00:01', 6120.72, 6120.72, 6112.52, 6113.36, '7853790', -1],
        // ['2011-01-01↵00:00:01', 6125.21, 6125.21, 6117.84, 6122.9, '6752770', -1],
        // ['2011-01-01↵00:00:02', 6126.52, 6126.52, 6117.13, 6118.74, '8879642', -1],
        // ['2011-01-01↵00:00:03', 6122.43, 6132.88, 6122.43, 6124.82, '8873609', 1],
        // ['2011-01-01↵00:00:03', 6130.41, 6132.85, 6125.35, 6132.85, '9006720', 1],
        // ['2011-01-01↵00:00:04', 6140.59, 6140.59, 6131.41, 6133.49, '6546873', -1],
        // ['2011-01-01↵00:00:04', 6145.02, 6145.02, 6139.21, 6142.76, '8402677', -1],
        // ['2011-01-01↵00:00:05', 6140.82, 6144.77, 6136.81, 6144.67, '8985545', 1],
        // ['2011-01-01↵00:00:06', 6139.8, 6139.8, 6131.13, 6137.7, '7979261', -1],
      ]
      data = this.lineList
      // console.log(data.length, 'data打印', data)
      if (data.length === 0) {
        console.log('他是空')
      } else {
        console.log('他是不是空')
        return data
      }
      var xValue = +new Date(2011, 0, 1)
      var minute = 60 * 10
      console.log(minute, 'minuteminuteminute')
      // var baseValue = Math.random() * 12000
      var baseValue = Math.random() * 12000
      var boxVals = new Array(4)
      var dayRange = 12
      console.log()

      for (var i = 0; i < count; i++) {
        console.log(xValue, 'xValuexValuexValue')
        baseValue = baseValue + Math.random() * 20 - 10

        for (var j = 0; j < 4; j++) {
          boxVals[j] = (Math.random() - 0.5) * dayRange + baseValue
        }
        boxVals.sort()
        console.log(boxVals, 'boxValsboxValsboxValsboxValsboxValsboxValsboxValsboxValsboxValsboxValsboxVals')
        var openIdx = Math.round(Math.random() * 3)
        console.log(openIdx, '123456')
        var closeIdx = Math.round(Math.random() * 2)
        if (closeIdx === openIdx) {
          closeIdx++
        }
        var volumn = boxVals[3] * (1000 + Math.random() * 500)
        data[i] = [
          echarts.format.formatTime('yyyy-MM-dd\nhh:mm:ss', xValue += minute),
          +boxVals[openIdx].toFixed(2), // open
          +boxVals[3].toFixed(2), // highest
          +boxVals[0].toFixed(2), // lowest
          +boxVals[closeIdx].toFixed(2), // close
          volumn.toFixed(0),
          // getSign(data, i, +boxVals[openIdx], +boxVals[closeIdx], 4) // sign
          getSign(i, +boxVals[openIdx], +boxVals[closeIdx], 4) // sign
        ]
      }
      console.log(data, 'data数据源来着')
      return data
      function getSign (dataIndex, openVal, closeVal, closeDimIdx) {
        console.log(dataIndex, openVal, closeVal, closeDimIdx)
        var sign
        if (openVal > closeVal) {
          sign = -1
        } else if (openVal < closeVal) {
          sign = 1
        } else {
          // sign = dataIndex > 0 ? (data[dataIndex - 1][closeDimIdx] <= closeVal ? 1 : -1) : 1
        }
        console.log(sign, 'signsignsignsignsign')
        return sign
      }
    },
    lineData () {
      var myChart = echarts.init(document.getElementById('chart_example'))
      var upColor = '#ec0000'
      var upBorderColor = '#8A0000'
      var downColor = '#00da3c'
      var downBorderColor = '#008F28'

      // var dataCount = 2e5
      var dataCount = 10
      console.log(dataCount, 'dataCount')
      var data = this.generateOHLC(dataCount)
      // var data = this.generateOHLC()

      var option = {
        backgroundColor: '#1b213b',
        dataset: {
          source: data
        },
        grid: [
          {
            left: '7%',
            top: '5%',
            right: '10%',
            bottom: 100
          },
          {
            left: '10%',
            right: '10%',
            height: 40,
            bottom: 0
          }
        ],
        xAxis: [
          {
            type: 'category',
            scale: true,
            boundaryGap: false,
            // inverse: true,
            axisLine: {
              onZero: false,
              lineStyle: {
                color: 'rgba(255,255,255,0.5)',
                with: 1
              }
            },
            border: 'red',
            axisLabel: {
              show: true,
              textStyle: {
                color: 'rgba(255,255,255,0.3)',
                fontSize: 10
              }
            },
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          },
          {
            type: 'category',
            gridIndex: 1,
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            axisTick: { show: false },
            splitLine: { show: false },
            axisLabel: { show: false },
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          }
        ],
        yAxis: [
          {
            position: 'right',
            axisLine: {
              lineStyle: {
                color: 'rgba(255,255,255,0.5)',
                with: 1
              }
            },
            scale: true,
            splitLine: { show: true },
            axisLabel: {
              show: true,
              textStyle: { color: 'rgba(255,255,255,0.3)' }
            },
            splitArea: {
              show: true,
              areaStyle: {
                color: [
                  'rgba(0,0,0,0)',
                  'rgba(0,0,0,0)'
                ]
              }
            }
          },
          {
            scale: true,
            gridIndex: 1,
            splitNumber: 2,
            axisLabel: { show: false },
            axisLine: { show: false },
            axisTick: { show: false },
            splitLine: { show: false }
          }
        ],
        dataZoom: [
          {
            type: 'inside',
            xAxisIndex: [0, 1],
            start: 10,
            end: 100
          }
          // {
          //   type: 'inside',
          //   xAxisIndex: [0, 1],
          //   start: 10,
          //   end: 100
          // },
          // {
          //   show: true,
          //   xAxisIndex: [0, 1],
          //   type: 'slider',
          //   bottom: 10,
          //   start: 10,
          //   end: 100,
          //   handleIcon: 'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
          //   handleSize: '105%'
          // }
        ],
        visualMap: {
          show: false,
          seriesIndex: 1,
          dimension: 6,
          pieces: [{
            value: 1,
            color: upColor
          }, {
            value: -1,
            color: downColor
          }]
        },
        series: [
          {
            type: 'candlestick',
            itemStyle: {
              color: upColor,
              color0: downColor,
              borderColor: upBorderColor,
              borderColor0: downBorderColor
            },
            encode: {
              x: 0,
              y: [1, 4, 3, 2]
            }
          },
          {
            name: 'Volumn',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            itemStyle: {
              color: '#7fbe9e'
              // color: ['rgba(255,0,0,1)']
            },
            large: true,
            encode: {
              x: 0,
              y: 5
            }
          }
        ]
      }
